export const get_inventory_by_prod = async (pId,cb) => {
    
    let bdyq2 = {...getStore(qryStore())?.get_inventory_prod};
    bdyq2.params = {
        businessId:  getStore("profileUser")?.businessId,
        ":productId": pId
    }
    const res = fetchEncryptStream(bdyq2);
    const td = await res;
    if (td) {
        updStore("inv_products_list", sortProducts(convertObj2Array(td.list),"name"));
        updStore("inv_products_stock", td.stock);
        cb && cb();
    }
};


