export const search_product = async (cb) => {

    let bdyq2 = {...getStore(qryStore())?.search_prod};
    bdyq2.params = {
        businessId:  getStore("profileUser")?.businessId,
        limit: 120
    }

   

    if(getStore("searchQry").length>2){
        getStore("searchQry").split(" ").map((qry,inDq)=>{
            if(qry){
                bdyq2.params[":search"+inDq] = qry.trim();
            }
        })
        const res = fetchEncryptStream(bdyq2);
        const td = await res;
        if (td) {
            updStore("products_list", sortProducts(convertObj2Array(td),"code"));
            cb && cb();
        }
    }else{
        updStore("products_list",null);
        cb && cb();
    }

};




