export function GroupbyAcc(arr, isPay) {
    let key =  "product";
    var obj = {
        "operations":[]
    };
    let balance = 0;

    let arr1 = arr && ObjectKeys(arr)
    if (arr1.length > 0) {
      arr1.map((ele) => {
        let s = arr[ele];
        if(s["type"]){
            if(isPay){
                balance += s["type"] === "debit" ? s["amount"] * -1 : s["amount"] * 1;
            }
            else{
                balance += s["type"] === "credit" ? s["amount"] * -1 : s["amount"] * 1;
            }
            obj["operations"].push(convert4acc(s));
        }
      });
    } else {
      obj = null;
    }
    return obj;
  }



  export function convert4acc(s) {
    let nItm = {};
    nItm["product"] =  getStore("products_ids")?.[s["product"]];
    nItm["sub_product"] = getStore("sub_products_ids")?.[s["subProduct"]];
    nItm["comprobanteId"] = s["comprobanteId"];
    nItm["document"] = s["document"];
    nItm["date"] = s["date"];
    if(s["type"]==="credit"){
        nItm["debit"] = 0;
        nItm["credit"] = s["amount"];
        
    }
    else if(s["type"]==="debit"){
        nItm["credit"] = 0;
        nItm["debit"] = s["amount"];
    }
    return nItm;
  }



  
//  sorting episodes comparing inputs to save in cache
export const sortProducts = (td, key) =>
callbackOnMemory(
	"sortProducts",
	[td],
	() => {
        let order = true;
		let sortedA = td && td.sort((a, b) => {
			let A = a[key];
			let B = b[key];
			if (A < B) {
				return order ? -1 : 1;
			}
			if (A > B) {
				return order ? 1 : -1;
			}
			return 0;
        });
		return sortedA;
	}
);




export const calcSf2fromInch = ({w,h}) =>
callbackOnMemory(
	"calcSf2fromInch",
	[w,h],
	() => {
        let square_inch = w*h;
		return square_inch;
	}
);




export const sortComprobantesByDate = (td) =>
callbackOnMemory(
	"sortComprobantesByDate",
	[td],
	() => {
        let order = true;
        let key = "date";
		let sortedA = td && td.sort((a, b) => {
			let A =(new Date(a[key])).getTime();
			let B = (new Date(b[key])).getTime();
			if (A < B) {
				return order ? -1 : 1;
			}
			if (A > B) {
				return order ? 1 : -1;
			}
			return 0;
        });
		return sortedA;
	}
);




export const calcSubmayor = (td, product, subProduct) =>
callbackOnMemory(
	"calcSubmayor",
    [td, product, subProduct],() => {
        let order = true;
        let key = "date";
		let sortedA = td.sort((a, b) => {
			let A =(new Date(a[key])).getTime();
			let B = (new Date(b[key])).getTime();
			if (A < B) {
				return order ? -1 : 1;
			}
			if (A > B) {
				return order ? 1 : -1;
			}
			return 0;
        });

       let prevBal = 0;

        const saldo = (item)=>{
            let itm = {...item};
            if(item?.product?.type==="credit"){
                itm["saldo"] = prevBal +  (itm["credit"]*1) - (itm["debit"]*1); 
            }else{
                itm["saldo"] = prevBal + (itm["debit"]*1) - (itm["credit"]*1); 
            }
           
            prevBal = itm["saldo"];
            return itm;
        }

		return sortedA.map((d)=>saldo(d));
	}
);





