import React, { useEffect, useState, useRef } from "react";



import { getStore, updStore, gen16CodeId, parseDate, fetchPostH, getUrlServerSide, fetchEncryptStream, ObjectKeys, genNumId, qryStore, parseFloatToDecimal, generateComprobanteId } from "../../../stores/Utils";

import { CheckBoxSlide_Cmpt, TextInput_Cmpt, CheckBox_Cmpt, SvgIcon_Cmpt, AccountLabelDetail_Cmpt } from "../../../components";


import "./styles.css"
import { CloseModal, OpenModal } from "../../../components/DialogHRM";

import { useNavigate } from "react-router";
import { SelectDropAccount_Cmpt, PickCustomer_Cmpt} from "../../ComprobanteParametrizados";

import { PickProduct_Cmpt, DateCalComponent_Cmpt } from "..";
import { get_prod_stock } from "../services/server_fetching";

const Svg = SvgIcon_Cmpt();

const CheckBoxSlide = CheckBoxSlide_Cmpt();
const TextInput = TextInput_Cmpt();

const DateCalComponent =DateCalComponent_Cmpt();


const PickProvide = PickCustomer_Cmpt();
const PickProduct =PickProduct_Cmpt();


const frmNm = "new_product_out_comprobante"

const AddOutProduct  = ({ data, confirm }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);

	const navigate = useNavigate();

	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			updStore("accountPicker", {});
			set_obs(gen16CodeId())
		}
	});

	const updFrm = (f, v, k) => {
		let frm2 = getStore(frmNm) || {};
		if(k){
			if( !frm2[k]){
				frm2[k] = {};
			}
			frm2[k][f] = v;
		}else{
			frm2[f] = v;
		}
		updStore(frmNm, frm2);
		
		set_obs(gen16CodeId())
	};


	const save  = async ()  => {
		let ffm = getStore(frmNm);

		let date =  (new Date()).getTime()+800;
		if(ffm["date"]){
			let nn = (new Date(ffm["date"])).getTime();
			date = nn?nn:date;
		}
		ffm["date"] = date; 
		let comp2save = [];
		
		let comprobanteId = await generateComprobanteId(ffm["date"]);
		ffm["comprobanteId"] = comprobanteId;
	
		let totalC = 0;
		let sumInv = 0;

		let dtAcc = getStore("global_account_params");

		let arr2 = getStore("productListOnOut") && ObjectKeys(getStore("productListOnOut"));
		arr2 && arr2?.map(async (element, indE)=>{
			
			let dataAcc = {...getStore("productListOnOut")[element]};
			
			let nnfrm = {...dataAcc};
			
			nnfrm["document"] = ffm["document"].trim();
			nnfrm["date"] = date;
			nnfrm["price"] = dataAcc["price"]*1;
			nnfrm["qty"] = dataAcc["qty"]*-1;
			nnfrm["comprobanteId"] = ffm["comprobanteId"]
			nnfrm["inventoryId"] = gen16CodeId();
			delete nnfrm["product"]
			delete nnfrm["account"]
			delete nnfrm["subAccount"]

			let amount = (dataAcc["qty"]*1)*(dataAcc["price"]*1);
			
			totalC +=  amount;
			sumInv +=  amount;
			nnfrm["amount"] = amount*-1;
			
			let bdyq2 = {...getStore(qryStore())?.add_inventory};
			bdyq2.params = {
				businessId:  getStore("profileUser")?.businessId
			}
			bdyq2.form=nnfrm;
			
			comp2save.push(bdyq2)
		});





		if(sumInv > 0){
			let nnfrm3 = {...ffm};
			nnfrm3["type"] =  "credit";
			nnfrm3["account"] = dtAcc?.inventory?.account;
			nnfrm3["amount"] =  sumInv;
			nnfrm3["operationId"] = gen16CodeId();
			
			let bdyq2 = {...getStore(qryStore())?.add_opera};
			bdyq2.params = {
				businessId:  getStore("profileUser")?.businessId
			}
			bdyq2.form=nnfrm3;
			comp2save.push(bdyq2)

			let nnfrm4 = {...ffm};
			nnfrm4["type"] =  "debit";
			nnfrm4["account"] = dtAcc?.inventory_expensess?.account;
			nnfrm4["amount"] =  sumInv;
			nnfrm4["operationId"] = gen16CodeId();
			
			let bdyq4 = {...getStore(qryStore())?.add_opera};
			bdyq4.params = {
				businessId:  getStore("profileUser")?.businessId
			}
			bdyq4.form=nnfrm4;
			comp2save.push(bdyq4)
			
		}


		if(totalC > 0 && getStore("customerOutPicker").account ){
			let nnfrm3 = {...ffm};
			nnfrm3["type"] =  "debit";
			nnfrm3["account"] =  getStore("customerOutPicker")?.["account"];
			nnfrm3["subAccount"] =  getStore("customerOutPicker")?.["subAccount"];
			nnfrm3["providerId"] =  getStore("customerOutPicker")?.["providerId"];
			nnfrm3["operationId"] = gen16CodeId();
			let accountDeb = getStore("account_ids")?.[getStore("customerOutPicker")?.["account"]];
			if(accountDeb && accountDeb.payOrCollect){
				nnfrm3["payOrCollect"] = accountDeb.payOrCollect;
			}
			nnfrm3["amount"] =  nnfrm3["amount"]*1;
			let bdyq2 = {...getStore(qryStore())?.add_opera};
			bdyq2.params = {
				businessId:  getStore("profileUser")?.businessId
			}
			bdyq2.form=nnfrm3;
			comp2save.push(bdyq2)
			
		}

		if(dtAcc?.sales_income?.account){
			let nnfrm6 = {...ffm};
			nnfrm6["type"] =  "credit";
			nnfrm6["account"] = dtAcc?.sales_income?.account;
			nnfrm6["operationId"] = gen16CodeId();
			nnfrm6["amount"] =  nnfrm6["amount"]*1;
			let bdyq6 = {...getStore(qryStore())?.add_opera};
			bdyq6.params = {
				businessId:  getStore("profileUser")?.businessId
			}
			bdyq6.form=nnfrm6;
			comp2save.push(bdyq6)
		}
		
		
		const promises = comp2save.map(async (qry2Sv) => {
			const res = fetchEncryptStream(qry2Sv);
			const td = await res;
			if (td) {
				//console.log(td)
			}
        })      
        await Promise.all(promises);
		

		data?.modalID && CloseModal({id:data?.modalID});
		
		setTimeout(() => {
			updStore("extraAccountListOnCmp", null);
			updStore("productListOnOut", null);
			updStore("customerOutPicker", null);
			updStore(frmNm, null)
			set_obs(gen16CodeId());
			confirm && confirm();
			navigate({
				pathname: "/accounting-ledger" ,
				search: `?cId=${comprobanteId}`
			});
		}, 400);
		/**
 */
	};






	return (
		<div style={{
			maxHeight: "90vh",
			minHeight:  "88vh",
			width: "750px",
			overflow: "auto",
			background: "var(--hrm_palette-general-background-b)",
			borderRadius: 13,
			padding: "10px 19px"
		}}>
			<div className="_dsplFlx ">
				<p className="form_title">
					Salida de Mercancias
				</p>
				<div className="flexSpace"/>
				
			</div>

			<div className="_dsplFlx ">
				<DateCalComponent init={getStore(frmNm)?.["date"]} updDate={(e)=>updFrm("date",e)} />
               

				<div className="flexSpace"/>
				<TextInput  
					init={getStore(frmNm)?.["document"]} 
					label={"Documento"} 
					width={120} 
					updChanges={(e)=>updFrm("document",e)}
				/>
				<div className="flexSpace"/>
				<TextInput  
					init={getStore(frmNm)?.["amount"]} 
					label={"Importe de la venta"} 
					width={120} 
					updChanges={(e)=>updFrm("amount",e)}
				/>
			</div>
			
			<TextInput  
				init={getStore(frmNm)?.["description"]} 
				label={"Descripcion"} 
				width={520} 
				updChanges={(e)=>updFrm("description",e)}
			/>
			<div className="_dsplFlx "  style={{margin: "18px 0"}}>
				<div className="flexSpace"/>
				<ProviderBox />
			</div>
			

			
			<ProductsBox />

			
			<div className="_dsplFlx ">
				<div className="flexSpace"/>
				{getStore(frmNm)?.["document"] &&  getStore(frmNm)?.["amount"] && getStore("customerOutPicker")?.account && ObjectKeys(getStore("productListOnOut"))?.length>0?
				<div className="card">
					<button onClick={save}>
						Guardar
					</button>
				</div>
				:null}
			</div>
		

			
		</div>
	);
};

export default AddOutProduct;





const EstaCuadrado = (obj) => {
	let balance = 0;
	let arr = obj && ObjectKeys(obj);
	arr && arr?.map((element, indE)=>{
		let data = obj[element];
		let newB = balance.toFixed(2);
		let amount = data?.amount ? data?.type === "debit" ?  parseFloat(data.amount) :parseFloat(data.amount) *-1  : 0;
		balance = amount + parseFloat(newB);
		
	});
	return arr && arr?.length>0 && balance === 0;
}  



const ProviderBox = ({}) => {
	const [obs, set_obs] = useState(0);


	
	const  handlePick = (v) =>{
		let nn= {
			"providerId":v.providerId,
			"account":v.account,
			"subAccount":v.subAccount,
		}
		updStore("customerOutPicker", nn);
		set_obs(gen16CodeId())
	}

	const  clear = () =>{
		updStore("customerOutPicker", null);
		set_obs(gen16CodeId())
	}

	let accountD = getStore("providers_ids")?.[getStore("customerOutPicker")?.providerId];
	

	return (
		<div >
				{accountD?.name ? 
				<div className="_dsplFlx " style={{margin: "18px 0"}}>
					<div className=" acc_dtls">
						<div className="_dsplFlx"  >
							<div className="code" style={{color: "#646cff"}} onClick={clear}>
								{accountD?.name} 
							</div>
						</div>
						<div className="flexSpace"/>
					</div>
					
				</div> :
				<PickProvide
					placeholder={"buscar una provedor"} 
					width={320}
					handleClick={handlePick}
				/>
				}
			</div>

	)
}











