const ProductsBox = ({}) => {
	const [obs, set_obs] = useState(0);

	const  updObs = () =>{
		set_obs(gen16CodeId())
    }
	
    const  clear = (v) =>{
        updStore("productPicker", null);
		set_obs(gen16CodeId())
    }
	
	const  handlePick = (v) =>{
		let nn= {
			"productId":v.productId,
			"product":v.product,
			"subAccount":v.subAccount,
		}
		updStore("productPicker", nn);
		get_prod_stock(v.productId,updObs);
	}


	let productD = getStore("products_ids")?.[getStore("productPicker")?.productId];
	
	
	const updFrmProducts = (f, v, k) => {
		let frm2 = getStore("productPicker") || {};
		if(k){
			if( !frm2[k]){
				frm2[k] = {};
			}
			frm2[k][f] = v;
		}else{
			frm2[f] = v;
		}
		updStore("productPicker", frm2);
		set_obs(gen16CodeId())
	};

	const addProduct2List = () => {
		let frm2 = {...getStore("productPicker")} || {};
		let rstFrm = {...getStore("productPicker")} || {};
		let productListOnOut = {...getStore("productListOnOut")} || {};
		let id = frm2["productId"];
		let price_cost = getStore("products_stock")?.[getStore("productPicker")?.productId]?.amount / getStore("products_stock")?.[getStore("productPicker")?.productId]?.qty;
	
		frm2["price"] = price_cost;
		productListOnOut[id] = frm2;
		updStore("productListOnOut", productListOnOut);
		rstFrm["productId"] = null;
		rstFrm["qty"] = "";
		rstFrm["price"] = "";
		updStore("productPicker", rstFrm);
		set_obs(gen16CodeId())
	}

	const rmvProduct2List = (id) => {
		let productListOnOut = getStore("productListOnOut") || {};
		delete productListOnOut[id];
		updStore("productListOnOut", productListOnOut);
		set_obs(gen16CodeId())
	}


	return (
		
		<div className="boxAddAcc" style={{margin: "18px 0"}}>

			{getStore("productPicker")?.productId ? 
			<div className="_dsplFlx " style={{margin: "18px 0"}}>
				<div className=" acc_dtls">
					<div className="_dsplFlx"   onClick={clear}>
						<div className="code" style={{color: "#646cff"}}>
							{productD?.upc_barcode} 
						</div>
						<div className="name">
							{productD?.name}
						</div>
					</div>
					<div className="flexSpace"/>
				</div>
			</div> :
			<PickProduct
				width={420}
				handleClick={handlePick}
			/>
			}
			
			{ getStore("products_stock")?.[getStore("productPicker")?.productId]?.qty? 
			<div className="_dsplFlx " >
				<TextInput  
					init={getStore("productPicker")?.["qty"]} 
					label={"Cantidad"} 
					width={120} 
					type={"number"}
					updChanges={(e)=>updFrmProducts("qty",e)}
				/>
				
				<div className="flexSpace"/>
				{getStore("productPicker")?.qty && getStore("productPicker")?.qty <= getStore("products_stock")?.[getStore("productPicker")?.productId]?.qty? 
				<div style={{marginTop:38}}>
					<button onClick={addProduct2List}>
						Agregar
					</button>
				</div>
				:null}
			</div>
			:null}
			<div className="_dsplFlx hdr_bar purple" style={{margin: "18px 0"}}>
				<div className="tr_bar tr_bar_50 ">
					Producto
				</div>
				<div className="tr_bar tr_bar_20">
					Qty
				</div>
				<div className="tr_bar tr_bar_20">
					Costo (un)
				</div>
				<div className="tr_bar tr_bar_5">
					
				</div>
			</div>
			{getStore("productListOnOut") && ObjectKeys(getStore("productListOnOut"))?.map((element, indE)=>
				<ListingProducts key={element} idKey={element} data={getStore("productListOnOut")[element]} deleteItem={rmvProduct2List}/>
			)}
		</div>

	)
}



const ListingProducts  = ({ data, deleteItem, idKey }) => {

	let productD = getStore("products_ids")?.[data?.productId];
	
	
	const editSearchAccount = () => {
		deleteItem(idKey)
	}
	
	
	return (
		<div className="_dsplFlx hdr_bar">
			
			<div className="tr_bar tr_bar_50">
				{productD?.name}
			</div>
			<div className="tr_bar tr_bar_20">
				{parseFloatToDecimal(data?.qty,2)}
			</div>
			<div className="tr_bar tr_bar_20">
				{parseFloatToDecimal(data?.price,2)}
			</div>
			<div className="flexSpace"/>
			<div className="icon" onClick={editSearchAccount}>
				<Svg name={"outline_delete"} color={"#c62828"}/>
			</div>

		</div>
	)

}


